import { yupResolver } from '@hookform/resolvers/yup'
import { Button, Stack, TextField } from '@mui/material'
import { DomainAPI } from 'API/domain'
import DialogConfirm from 'components/DialogConfirm'
import { useState } from 'react'
import { SubmitHandler, useForm } from 'react-hook-form'
import { useAppDispatch } from 'redux/hooks'
import { UIAction } from 'redux/slices/UI'
import * as yup from 'yup'

interface FormInput {
	name: string
	displayName: string
	clientId: string
	tenantId: string
}

export default function AddDomain() {
	const [show, setShow] = useState(false)
	const [loading, setLoading] = useState(false)
	const dispatch = useAppDispatch()

	const schema = yup.object({
		name: yup.string().required(),
		clientId: yup.string().required(),
		tenantId: yup.string().required(),
		displayName: yup.string().required(),
	})
	const {
		register,
		handleSubmit,
		formState: { errors },
	} = useForm<FormInput>({
		resolver: yupResolver(schema),
		defaultValues: {
			name: '',
			clientId: '',
			tenantId: '',
			displayName: '',
		},
	})

	const onSubmit: SubmitHandler<FormInput> = async (data) => {
		try {
			setLoading(true)
			const { error, message } = await DomainAPI.create(data)
			dispatch(
				UIAction.addGlobalMessage({
					type: error ? 'error' : 'success',
					message: message,
				})
			)
			setShow(false)
		} finally {
			setLoading(false)
		}
	}
	return (
		<Stack>
			<Button
				variant="contained"
				size="small"
				color="success"
				sx={{
					minWidth: 120,
				}}
				onClick={() => setShow(!show)}
			>
				新しいドメイン追加
			</Button>
			<DialogConfirm
				open={show}
				loading={loading}
				handleClose={() => setShow(false)}
				title="ドメイン追加"
				disabled={Object.keys(errors).length > 0}
				primaryButtonText="追加"
				primaryButtonColor="success"
				component="form"
				maxWidth="sm"
				handleSubmit={handleSubmit(onSubmit)}
			>
				<Stack gap={1}>
					<TextField
						variant="standard"
						autoFocus
						required
						margin="dense"
						fullWidth
						size="small"
						label={'ドメイン名'}
						placeholder="ex:caa.go.jp"
						InputLabelProps={{ shrink: true }}
						{...register('name')}
						error={'name' in errors}
					/>
					<TextField
						variant="standard"
						required
						margin="dense"
						fullWidth
						size="small"
						label={'表示名'}
						placeholder="ex:消費者庁"
						InputLabelProps={{ shrink: true }}
						{...register('displayName')}
						error={'displayName' in errors}
					/>
					<TextField
						variant="standard"
						required
						margin="dense"
						fullWidth
						size="small"
						label={'クライアントID'}
						{...register('clientId')}
						error={'clientId' in errors}
					/>
					<TextField
						variant="standard"
						required
						margin="dense"
						fullWidth
						size="small"
						label={'テナントID'}
						{...register('tenantId')}
						error={'tenantId' in errors}
					/>
				</Stack>
			</DialogConfirm>
		</Stack>
	)
}
