import { Stack, Typography } from '@mui/material'
import DeleteButton from 'components/Button/Delete'
import EditInput from 'components/EditInput'
import { Link, useLocation } from 'react-router-dom'
import { useAppSelector } from 'redux/hooks'
import AddDomain from './AddDomain'

export default function SettingPage() {
	const location = useLocation()
	const config = useAppSelector((s) => s.UI.config)
	const domains = useAppSelector((s) => s.UI.allowDomains)
	return (
		<Stack>
			<Stack
				direction="row"
				alignItems="center"
				gap={2}
				sx={{
					p: 1,
					bgcolor: '#ccc',
				}}
			>
				<Typography variant="h6">環境変数</Typography>
			</Stack>
			<Stack p={1} gap={1} mb={2}>
				<EditInput
					label="パスワードルール"
					value={config.passwordRole}
					apiEndpoint={'/api/config'}
					fieldName="passwordRole"
				/>
				<EditInput
					label="TokenAge"
					value={`${Number(config.signinAge) / (1000 * 60 * 60)}`}
					subText="時間"
					apiEndpoint={'/api/config'}
					fieldName="signinAge"
				/>
				<EditInput
					label="CookieMaxAge"
					value={`${Number(config.cookieMaxAge) / (1000 * 60 * 60)}`}
					subText="時間"
					apiEndpoint={'/api/config'}
					fieldName="cookieMaxAge"
				/>
				<EditInput
					label="ロック時間"
					value={`${Number(config.blockTime) / (1000 * 60 * 60)}`}
					subText="時間"
					apiEndpoint={'/api/config'}
					fieldName="blockTime"
				/>
				<EditInput
					label="パスワード記憶"
					value={config.passwordHistoryMax}
					apiEndpoint={'/api/config'}
					subText="回"
					fieldName="passwordHistoryMax"
				/>
				<EditInput
					label="パスワード再設定時間"
					value={`${Number(config.requireUpdatePassTime) / (1000 * 60 * 60 * 24)}`}
					apiEndpoint={'/api/config'}
					subText="日"
					fieldName="requireUpdatePassTime"
				/>
				<EditInput
					label="ログイン失敗数"
					value={config.fallMax}
					subText="回"
					apiEndpoint={'/api/config'}
					fieldName="fallMax"
				/>
			</Stack>
			<Stack
				direction="row"
				alignItems="center"
				gap={2}
				sx={{
					p: 1,
					bgcolor: '#ccc',
				}}
			>
				<Typography variant="h6">使用ドメイン</Typography>
				<AddDomain />
			</Stack>
			<Stack p={1} gap={1}>
				{domains.map((v) => {
					return (
						<Stack
							key={v.id}
							gap={1}
							direction="row"
							sx={{
								p: 1,
								border: '1px solid #ccc',
								boxShadow: '0 2px 4px rgba(0,0,0,0.2)',
							}}
						>
							<Stack flex={1}>
								<EditInput
									label="表示名："
									value={v.displayName}
									apiEndpoint={`/api/domain/${v.id}`}
									fieldName="displayName"
								/>
								<EditInput label="ドメイン：" value={v.name} apiEndpoint={`/api/domain/${v.id}`} fieldName="name" />
								<EditInput
									label="クライアントID："
									value={v.clientId}
									apiEndpoint={`/api/domain/${v.id}`}
									fieldName="clientId"
								/>
								<EditInput
									label="テナントID："
									value={v.tenantId}
									apiEndpoint={`/api/domain/${v.id}`}
									fieldName="tenantId"
								/>
							</Stack>
							<Stack>
								<Link to={`/setting/domain/${v.id}/delete`} state={{ location }}>
									<DeleteButton text="削除" sx={{ width: 150, py: '2px', fontSize: 14 }} />
								</Link>
							</Stack>
						</Stack>
					)
				})}
			</Stack>
		</Stack>
	)
}
